import CIcon from '@coreui/icons-react';
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import React, { useState } from 'react';

const Register = () => {
  const initialState = {userName: '', email: '', password: '', repeatPassword: ''};
  const [isState, setIsState] = useState(initialState);
  const {userName, email, password, repeatPassword} = isState;
  const [error, setError] = useState({userName: false, email: false, password: false, repeatPassword: false });


  const handleInputs = (e) => {
    setError({ ...error,  })
    if (e.target.name === 'userName') {
      e.target.value === '' ? setError({...error, userName: true}) : setError({...error, userName: false}) 
    } else if (e.target.name  === 'email') {
      e.target.value === '' ? setError({...error, email: true}) : setError({...error, email: false}) 
    } else if (e.target.password  === repeatPassword) {
      e.target.value === '' ? setError({...error, password: true}) : setError({...error, password: false}) 
    }
    setIsState({ ...isState, [e.target.name]: e.target.value });
  }

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="9" lg="7" xl="6">
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <CForm>
                  <h1>Register</h1>
                  <p className="text-muted">Create your account</p>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-user" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Username" autoComplete="username"
                     className={`form-control ${error.userName ? 'is-invalid' : 'is-valid'}`} value={userName} name="userName" onChange={handleInputs} required />
                     {/* {error && (
                    <div className="invalid-fedback">username is required</div>
                     )} */}
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>@</CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="text" placeholder="Email" autoComplete="email" 
                    className={`form-control ${error.email ? 'is-invalid' : 'is-valid'}`} value={email} name="email" onChange={handleInputs} required />
                     {/* {error && (
                    <div className="invalid-fedback">email is required</div>
                     )} */}
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-lock-locked" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="password" placeholder="Password" autoComplete="new-password"
                      className={`form-control ${error.password ? 'is-invalid' : 'is-valid'}`} value={password} name="password" onChange={handleInputs} required />
                  </CInputGroup>
                  <CInputGroup className="mb-4">
                    <CInputGroupPrepend>
                      <CInputGroupText>
                        <CIcon name="cil-lock-locked" />
                      </CInputGroupText>
                    </CInputGroupPrepend>
                    <CInput type="password" placeholder="Repeat password" autoComplete="new-password"
                     className={`form-control ${error.password ? 'is-invalid' : 'is-valid'}`} value={password} name="password" onChange={handleInputs} required />
                  </CInputGroup>
                  <CButton color="success" block>Create Account</CButton>
                </CForm>
              </CCardBody>
              <CCardFooter className="p-4">
                <CRow>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-facebook mb-1" block><span>facebook</span></CButton>
                  </CCol>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-twitter mb-1" block><span>twitter</span></CButton>
                  </CCol>
                </CRow>
              </CCardFooter>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Register
